.heading_decoration-underline {
  padding: 0;
  width: 235px;
  height: 2px;
  flex-shrink: 0;
  border: 0;
  background: rgba(218, 8, 8, 1);
  margin: auto;
}
.common-btn {
  text-decoration: none;
  background: var(--redcolor);
  display: inline-flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  border-radius: 7px;
  font-weight: 500;
