/* Header style*/
header {
  padding: 10px 0;
  z-index: 9;
}
.mainHeader.stickyHeader {
  background: #062d60;
  position: fixed !important;
  top: 0 !important;
  padding: 5px 0;
}
.header-btn {
  color: #fff;
  border: 1px solid #fff;
  font-size: 14px;
  font-family: var(--poppins);
  min-width: 159px;
  border-radius: 50px;
}
.header-btn:hover {
  background: var(--redcolor);
  border-color: var(--redcolor);
}
header.mainHeader .collapse ul.navbar-nav > li {
  margin-left: 65px;
}
#navbarSupportedContent ul li a {
  font-size: 14px;
  font-weight: 600;
  font-family: var(--poppins);
  padding: 5px 0;
  color: #fff;
  position: relative;
}
#navbarSupportedContent ul li a::after {
  position: absolute;
  content: "";
  background: var(--redcolor);
  width: 100%;
  height: 2px;
  left: 0;
  transform: translateY(5px);
  transition: 0.6s;
  opacity: 0;
  bottom: 0px;
}
#navbarSupportedContent ul li a:hover::after, #navbarSupportedContent ul li.current-menu-item a::after {
  transform: translateY(0px);
  opacity: 1;
}
#navbarSupportedContent ul li a:hover, #navbarSupportedContent ul li.current-menu-item a {
  color: var(--redcolor);
}
/* Banner */
.bannerSection {
