/* gateway section style */
.header-sec-common {
  text-align: center;
}
.header-sec-common h5 {
  padding: 0 0 30px;
  position: relative;
}
.header-sec-common h5::after {
  position: absolute;
  content: "";
  background: var(--redcolor);
  width: 100%;
  height: 2px;
  left: 50%;
  bottom: 12px;
  max-width: 236px;
  transform: translateX(-50%);
}
.gateway-flex {
  padding: 45px 0 0;
  align-content: stretch;
}
.gateway-flex > div {
  width: 100%;
  max-width: 48%;
  max-height: 490px;
}
.gateway-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.gateway-content h2 {
  margin: 0 0 20px;
  color: var(--blueColor);
  max-width: 800px;
}
.gateway-content p {
  margin: 0 0 45px;
  max-width: 890px;
}
.gateway-content p > a {
  color: #000;
  font-family: var(--poppins);
  margin-top: 25px;
}
.gateway-content p > a span {
  width: 54px;
  height: 54px;
  background: var(--redcolor);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  margin-left: 15px;
  transform: translateX(0px);
  transition: all 0.5s;
}
.gateway-content p > a:hover span {
  transform: translateX(10px);
  transition: all 0.5s;
}
.gateway-sec .gateway-content:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.80);
  z-index: 1;
}
.gateway-sec .gateway-content {
  position: relative;
  padding: 0px 45px 0 0;
  max-width: 60%;
}
.gateway-sec .gateway-content * {
  z-index: 1;
  position: relative;
}
.gateway-content > a {
  font-size: 21px;
  color: #000;
  font-family: var(--poppins);
}
.gateway-content > a span {
  width: 54px;
  height: 54px;
  background: var(--redcolor);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  margin-left: 15px;
}
.gateway-content > a:hover span {
  transform: translateX(10px);
  transition: all 0.5s;
}
.gateway-content a span i {
  font-weight: 900;
